.tableWrapper {
  width: 70em;
  margin-top: 25px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  .header {
    display: flex;
    justify-content: space-between;
    background: #f1f1f4;
    padding: 9px 8px 5px;
    font-size: 10px;
    font-weight: bold;
    font-family: sans-serif;
    color: #556789;
    .date {
      width: 20%;
    }
    .campaign {
      width: 30%;
    }
    .view {
      width: 15%;
    }
    .action {
      width: 45%;
      margin-left: 15px;
    }
  }
}
@media (min-width: 300px) and (max-width: 500px) {
  .tableWrapper {
    width: 38em;
  }
}
